import { Fragment, useEffect, useState } from "react";
import Header from "./components/Header";
import Formulario from "./components/Formulario";
import ListadoNoticias from "./components/ListadoNoticias";

function App() {

  // Definir la categoria y noticias
  const [ categoria, setCategoria ] = useState( '' );
  const [ noticias, setNoticias ] = useState( [] );

  // Consulta a la API para obtener los resultados de la búsqueda
  useEffect( () => {

    const consultarAPI = async () => {
      const url = `https://newsapi.org/v2/top-headlines?country=mx&category=${ categoria }&apiKey=e52aa4c34dfa4e108f278d9faba7b903`;

      const respuesta = await fetch( url );
      const noticias = await respuesta.json();

      setNoticias( noticias.articles );

    }

    consultarAPI();

  }, [ categoria ] )

  return (
    <Fragment>
      <Header
        titulo="Buscador de Noticias en México"
      />
      <div className="container white">
        <Formulario
          setCategoria={ setCategoria }
        />

        <ListadoNoticias
          noticias={ noticias }
        />

      </div>
    </Fragment>
  );
}

export default App;
